import styled from "styled-components";

const Container = styled("div")`
width: 100vw;
height: auto;
background-color: #192841;

margin-top:200px;
padding: 10px 0;

display: flex;
flex-direction: column;
justify-content: center;
align-items: center;

`
const ListItem = styled("li")`
margin: 10px;
color: #FFA500;

`
const BigTitle = styled("h1")`
color: #ff8c00;
`
const UnderTitle = styled("h2")`
color: #ff8c00;
`
const Roadmap = styled("img")`
width :auto;
height: 1100px;
object-position: bottom;
margin-top: 30px;
border-radius:10%;
`


const OurVision = () => {
return(
<Container id="OurVision">
<BigTitle>
    Our Vision
</BigTitle>
<UnderTitle>
   Information
</UnderTitle>
<ul style =  {{ padding: "0px 20%"}}>
<ListItem >
    This project consist of 200 pixelated Giraffes on the solana Blockchain ! 
</ListItem>
<ListItem>
This is our FIRST NFT project so ALL wallets who mint will receive whitelist tokens for all future projects released!

</ListItem>
<ListItem>
The 2d_Giraffes is the first project of the 2d_Animals series! In the near future we will also release more projects such as the 2d_Bunnies and 2d_Lions!
</ListItem>
<ListItem>
    40% of all secondary sales of our nft will be used for the 2d_Animals DAO. This DAO will be made available after we release our third Project
</ListItem>

</ul>
<UnderTitle>
    Roadmap
</UnderTitle>
<Roadmap src="/Roadmap.png" alt ="Roady"/>
</Container>


)

}
export default OurVision;